// 1. set(key,value)key  value 
// 2. remove(key)
// 3. hasKey(key)truefalse
// 4. get(key)
// 5. clear()
// 6. size()length 
// 7. isEmpty()size truefalse
// 8. keys()
// 9. values()
// 10. keyValues()[]
// 11. forEach(callbackFn)callbackFn key  valuefalse Array every 

import { defaultToString } from "../utils/index.js";

export class ValuePair {
  constructor(key, value) {
    this.key = key;
    this.value = value;
  }
  toString() {
    return `[#${this.key}, ${this.value}]`;
  }
}

export class Dictionary {
  constructor(toStrFn = defaultToString) {
    this.toStrFn = toStrFn;
    this.table = {};
  }

  // key  value 
  set(key, value) {
    if (key != null && value != null) {
      const tableKey = this.toStrFn(key);
      this.table[tableKey] = new ValuePair(key, value);
      return true;
    }
    return false;
  }

  // remove(key)
  remove(key) {
    if (this.hasKey(key)) {
      delete this.table[this.toStrFn(key)];
      return true;
    }
    return false;
  }

  // truefalse
  hasKey(key) {
    const tableKey = this.toStrFn(key);
    return this.table[tableKey] != null;
  }

  // 
  get(key) {
    if (this.hasKey(key)) {
      return this.table[this.toStrFn(key)].value;
    }
    return;
  }

  // 
  clear() {
    this.table = {};
  }

  // length 
  size() {
    return Object.keys(this.table).length;
  }

  // size truefalse
  isEmpty() {
    return this.size() === 0;
  }

  // 
  keys() {
    return this.keyValues.map((valuePair) => valuePair.key);
  }

  // 
  values() {
    return this.keyValues.map((valuePair) => valuePair.value);
  }

  // []
  keyValues() {
    return Object.values(this.table);
  }

  // callbackFn key  valuefalse Array every 
  forEach(callbackFn) {
    const valuePairs = this.keyValues();
    for (let i = 0; i < valuePairs.length; i++) {
      const result = callbackFn(valuePairs[i].key, valuePairs[i].value);
      if (result === false) {
        break;
      }
    }
  }
}
