import { defaultEquals } from "../utils/index.js";

/**
 * 1push(element)
 * 2insert(element, position)
 * 3getElementAt(index)  undefined
 * 4remove(element)
 * 5indexOf(element)-1
 * 6removeAt(position)
 * 7isEmpty()  true 0 false
 * 8size() length 
 * 9toString() Node  JavaScript  toString 
 *
 */

export class Node {
  constructor(element) {
    this.element = element;
    this.next = null;
  }
}

export class LinkList {
  constructor(equalsFn = defaultEquals) {
    this.count = 0;
    this.head = null;
    this.equalsFn = equalsFn;
  }

  // 
  push(element) {
    const node = new Node(element);
    let current;
    if (this.head === null) {
      this.head = node;
    } else {
      current = this.head;
      while (current.next !== null) {
        current = current.next;
      }
      current.next = node;
    }
    this.count++;
  }

  // 
  insert(element, index) {
    if (index < 0 || index > this.count) {
      return false;
    }

    const node = new Node(element);
    if (index === 0) {
      node.next = this.head;
      this.head = node;
    } else {
      const previous = this.getElementAt(index - 1);
      node.next = previous.next;
      previous.next = node;
    }
    this.count++;
  }

  //   undefined
  getElementAt(index) {
    if (index >= 0 && index <= this.count) {
      let node = this.head;
      for (let i = 0; i < index && node !== null; i++) {
        node = node.next;
      }
      return node;
    }
    return;
  }

  // 
  remove(element) {
    const index = this.indexOf(element);
    return this.removeAt(index);
  }

  // -1
  indexOf(element) {
    let current = this.head;
    for (let i = 0; i < this.count; i++) {
      if (this.equalsFn(element, current.element)) {
        return i;
      }
      current = current.next;
    }
    return -1;
  }

  // 
  removeAt(index) {
    if (index < 0 || index >= this.count) {
      return;
    }
    let current = this.head;
    if (index === 0) {
      this.head = current.next;
    } else {
      let previous = this.getElementAt(index - 1);
      current = previous.next;
      previous.next = current.next;
    }
    this.count--;
    return current.element;
  }

  //  true 0 false
  isEmpty() {
    return this.size() === 0;
  }

  //  length 
  size() {
    return this.count;
  }

  // 
  clear() {
    this.head = undefined;
    this.count = 0;
  }

  //  Node  JavaScript  toString 
  toString() {
    if (this.head === null) {
      return "";
    }
    let objString = `${this.head.element}`;
    let current = this.head.next;
    for (let i = 0; i < this.count && current != null; i++) {
      objString = `${objString}, ${current.element}`;
      current = current.next;
    }
    return objString;
  }
}
