// put(key,value)
// remove(key)
// get(key)

import { defaultToString } from "../utils/index.js";
import { ValuePair } from "./14.Dictionary.js";

export class HashTable {
  constructor(toStrFn = defaultToString) {
    this.toStrFn = toStrFn;
    this.table = {};
  }

  // 
  loseloseHashCode(key) {
    if (typeof key === "number") {
      return key;
    }
    const tableKey = this.toStrFn(key);
    let hash = 0;
    for (let i = 0; i < tableKey.length; i++) {
      hash += tableKey.charCodeAt(i);
    }
    return hash % 99;
  }

  hashCode(key) {
    return this.loseloseHashCode(key);
  }

  // 
  put(key, value) {
    if (key != null && value != null) {
      const hashKey = this.hashCode(key);
      this.table[hashKey] = new ValuePair(key, value);
    }
  }

  // 
  remove(key) {
    const hashKey = this.hashCode(key);
    const valuePair = this.table[hashKey];
    if (valuePair != null) {
      delete this.table[hashKey];
      return true;
    }
    return false;
  }

  // 
  get(key) {
    const hashKey = this.hashCode(key);
    const valuePair = this.table[hashKey];
    return valuePair ? valuePair.value : undefined;
  }
}
